import React, { Component } from "react";
import kodeBarang from "../Images/kode_barang.svg";
import kelompok from "../Images/kelompok.svg";
import kategori from "../Images/kategori.svg";
import { Link } from "react-router-dom";

class KlasifikasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="row col-lg-12">
          <div className="col-lg-4">
            <Link to="kelompok">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kelompok} width="300" alt="Kode Barang" />
                  <h3 style={{ color: "#000" }}>Kelompok</h3>
                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-lg-4">
            <Link to="kategori">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kategori} width="300" alt="Kategori" />
                  <h3 style={{ color: "#000" }}>Kategori</h3>

                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-lg-4">
            <Link to="kodebarang">
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={kodeBarang} width="300" alt="Kode Barang" />
                  <h3 style={{ color: "#000" }}>Kode Barang</h3>
                  <button className="btn btn-primary">Masuk</button>
                </div>
              </div>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default KlasifikasiBarang;
